import * as React from 'react';
import { useSearchParams } from 'react-router-dom';

import lang from './views.lang';

import Staff from '@miq/staffjs';
import { Response } from '@miq/utiljs';
import { Loading, Pagination } from '@miq/componentjs';

// import { useCrudEmitter } from '../events';

import { useProductListRequest } from './utils';
import { ProductListItem } from './components';
import { useDocumentTitle } from '@miq/hookjs';
import { TProduct } from '../types';
import { ProductListFilterForm } from './forms/form.PListFilter';

const { i18n } = window;

export default function ProductListView() {
  const [params] = useSearchParams();
  const { res, loading } = useProductListRequest();
  useDocumentTitle('Products - Store');

  if (loading) return <Loading />;

  const r = new Response<TProduct>(res);
  if (!r.isSuccess) return null;

  return (
    <Staff.View
      title={i18n(lang, 'ListView.title')}
      back="/staff/store/"
      actions={
        <Staff.Link to="new/" requiredPerms={['store.add_product']} className="btn btn-primary-3">
          {i18n(lang, 'ListView.actionLinkLabel')}
        </Staff.Link>
      }
      requiredPerms={['store.view_product']}
    >
      {r.items.length === 0 ? (
        <div className="text-center p-staff-list-empty py-3 my-3">
          <p className="fw-light text-md">{i18n(lang, 'ListView.noItemText')}</p>
          <div className="my-4">
            <Staff.Link to="new/" requiredPerms={['store.add_product']} className="btn btn-primary btn-md">
              {i18n(lang, 'ListView.addProductLinkLabel')}
            </Staff.Link>
          </div>
        </div>
      ) : (
        <>
          <ProductListFilterForm
            categories={r.data?.categories}
            fields={['category', 'published', 'sale', 'presale', 'nodesc', 'nosize', 'oos']}
          />

          {r.items.map((item) => (
            <ProductListItem item={item} {...{ params, stages: r.data?.stages }} key={item.slug} />
          ))}

          <div>
            <Pagination {...r.data} component={Staff.Link} to />
          </div>
        </>
      )}
    </Staff.View>
  );
}
